<template>
	<view>
		<cu-custom bgColor="bg-f6" :isBack="true">
			<block slot="backText"></block>
			<block slot="content">充值</block>
		</cu-custom>
		<view class="container">
			<view class="fs-28 lh-28 fc-303 mb20">充值金额</view>
			<view class="form-group">
				<input placeholder="请输入充值金额" name="input" placeholder-class="fc-c4cc" v-model="model.money" >
			</view>
			<view class="fs-28 lh-28 fc-303 mb20">选择支付方式</view>
			<view class="pay-way mb16">
				<view class="flex-between ac" v-for="item in channelData">
					<view class="dflex ac">
						<image :src="item.channelIcon" mode="" class="m-icon mr32"></image>
						<view class="fs-28 fc-303">{{item.channelName}}</view>
					</view>
					<label for="">
						<checkbox class="hh-radio fs-0 mr16 round" :checked="item.channelCheck==1?true:false" @click="onChannelSelect(item)">
						</checkbox>
					</label>
				</view>
				<!-- <view class="flex-between ac">
					<view class="dflex ac">
						<image src="../../../static/img/icon/pay-zfb@2x.png" mode="" class="m-icon mr32"></image>
						<view class="fs-28 fc-303">支付宝支付</view>
					</view>
					<label for="">
						<checkbox class="hh-radio fs-0 mr16 round" :class="isCheck?'checked':''" :checked="isCheck">
						</checkbox>
					</label>
				</view> -->
				<!-- <view class="flex-between ac">
					<view class="dflex ac">
						<image src="../../../static/img/icon/pay-bag@2x.png" mode="" class="m-icon mr32"></image>
						<view class="fs-28 fc-303 dflex">余额支付 (<view class="dflex ai-fe">
								<view class="fs-24 fc-303">￥</view>
								<view class="fs-28 lh-28 fc-303 mb2">{{userBalance.money}}</view>
							</view>)</view>
					</view>
					<label for="">
						<checkbox class="hh-radio fs-0 mr16 round" :class="isCheck?'checked':''" :checked="isCheck">
						</checkbox>
					</label>
				</view> -->
			</view>
			<label class="dflex jc-fs ac wp100 mb80">
				<checkbox class="hh-radio fs-0 mr16 round" :class="model.payCheck?'checked':''" :checked="model.payCheck"  @click="model.payCheck = !model.payCheck"></checkbox>
				<view class="fs-22 lh-32 fc-939">我已阅读并同意 <text class="fc-2e8" @click.stop="toAgreement">《支付协议》</text></view>
			</label>
			<button class="cu-btn border-btn bg-pp fc-f lg wp100" @click="onRecharge()">提交</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				channelData:[],
				model:{
					payCheck:true,
					money:'',
					payId:'',
					channelType:''
				}
			}
		},
		created:function(){
			var self = this;
			uni.$api.getPayChannel({}).then(res =>{
				self.channelData = res.data;
			});
		},
		methods: {
			toAgreement () {
				uni.navigateTo({
					url:"/pages/login/agreement/agreement?agreementId=3"
				})
			},
			onChannelSelect:function(item){
				// for(var i = 0;i<this.channelData.length;i++){
				// 	this.channelData[i].channelCheck = 0;
				// 	if(this.channelData[i].channelType == val){
				// 		this.channelData[i].channelCheck =1;
				// 	}
				// }
				this.channelData.map(e => {
					if (e.payId == item.payId){
						e.channelCheck = e.channelCheck==1?0:1
					}
					else {
						e.channelCheck=0
					}
					return e
				})
			},
			onRecharge:function(){
				
				if(this.model.money == ''){
					uni.$toast.showError('请输入充值金额！');
					return;
				}
				if(!this.model.payCheck){
					uni.$toast.showError('请勾选同意支付协议！');
					return;
				}
				// for(var i = 0;i<this.channelData.length;i++){
				// 	if(this.channelData[i].channelCheck == 1){
				// 		this.model.payId = this.channelData[i].payId;
				// 		this.model.channelType = this.channelData[i].channelType;
				// 	}
				// }
				var item = this.channelData.find(e =>{
					return e.channelCheck == 1
				})
				if (!item) {
					uni.$toast.showToast('请选择支付方式')
					return 
				}
				this.model.payId = item.payId;
				this.model.channelType = item.channelType;
				
				var self = this;
				uni.$api.payRecharge(this.model).then(res =>{
					console.log(JSON.stringify(res.data))
					var istest = false;
					if(res.hasOwnProperty("data")){
						if(res.data.hasOwnProperty("package")){
							if(res.data.package.indexOf("test_")>=0){
								istest = true;
							}
						}
					}
					
					if(istest){   //测试支付
						
						uni.$api.testPay({
						  'timeStamp': res.data.timeStamp,
						  'nonceStr': res.data.nonceStr,
						  'package': res.data.package,
						  'paySign': res.data.paySign
						}).then(notifyres => {
						  if (notifyres.success) {
						    uni.$toast.showToast('测试支付成功');
							uni.navigateBack();
						  }else{
							  uni.$toast.showError('测试支付失败！');
						  }
						});
						
					}else{  //走真实支付接口
						
						if(self.model.channelType < 4){
							var provider = "wxpay";
							switch(self.model.channelType){
								case 2: provider = "wxpay"; break;
								case 3: provider = "alipay"; break;
							}
							uni.requestPayment({
							    provider: provider,
							    orderInfo: res.orderInfo, //微信、支付宝订单数据 【注意微信的订单信息，键值应该全部是小写，不能采用驼峰命名】
							    success: function (res) {
							        console.log('success:' + JSON.stringify(res));
									uni.$toast.showToast('充值成功！');
									uni.navigateBack();
							    },
							    fail: function (err) {
							        console.log('fail:' + JSON.stringify(err));
									uni.$toast.showToast(JSON.stringify(err))
							    }
							});
						}
					}
				})
			}
		}
	}
</script>

<style>
page {
	background-color: #fff;
}
.container {
	padding: 32rpx 60rpx;
}
.pay-way {
	background-color: #fafafa;
	border-radius: 20rpx;
	padding: 0 32rpx;
}
.pay-way > view {
	padding: 32rpx 0 28rpx;
}
.border-btn {
	border-radius: 45rpx;
}
</style>
